.overall {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
}

.breadcrumb {
    margin-top: 24px;
    margin-left: 27.8px;
    list-style: none;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

.list-item {
    display: inline;
    font-size: 24px;
}

.breadcrumb .list-item+.list-item:before {
    padding: 8px;
    content: "/\A0";
}

.links-black {
    color: #000;
    text-decoration: none;
}

.links {
    text-decoration: none;
    color: #81878C;
}



.content {
    float: right;
    width: calc(100% - 256px);
    background: #fff;
    height: 100vh;
}
